import React, { PureComponent } from 'react'
import { Table, Form, Input } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
const FormItem = Form.Item
@connect(
  (state) => ({ ...state.creatTransferOrder, userLoation: state.userLoation }),
  mapDispatchToProps
)
class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      twoLevelSelected: 'all'
    }
  }
  componentDidMount() {
    const { update } = this.props
    update({
      modalData: {
        planData: { value: '100', children: '100%' },
        twoLevel: { value: 'all', children: '全部' }
      }
    })
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  handleSelectChange = (data) => {
    const { update, importResultList = [] } = this.props
    const { value, children } = data
    let arrResult = [],
      arrCopy = JSON.parse(JSON.stringify(importResultList))
    if (arrCopy && arrCopy.length > 0) {
      arrCopy.map((item, index, arr) => {
        item.planQty = (item.planQty * Number(value)) / 100
        arrResult.push(item)
      })
    }
    return update({
      modalData: {
        planData: children
      },
      importResultList: arrResult
    })
  }
  handleSelectBomLevel = (data) => {
    const { value = '' } = data
    return this.setState({ twoLevelSelected: value })
  }
  changePlanData = (dataIndex, e) => {
    const { update, importResultList = [] } = this.props
    let value = e.target.value
    let arrResult = [],
      arrCopy = JSON.parse(JSON.stringify(importResultList))
    if (arrCopy && arrCopy.length > 0) {
      arrCopy.map((item, index, arr) => {
        if (dataIndex == index) {
          item.number = Number(value) ? Number(value) : 0
        }
        arrResult.push(item)
      })
    }
    return update({ importResultList: arrResult })
  }
  renderControledTable = (importResultList, twoLevelSelected) => {
    let importResultListToShow = []
    if (importResultList && importResultList.length > 0) {
      if (twoLevelSelected === 'all') {
        importResultListToShow = importResultList
      } else {
        importResultListToShow = importResultList.filter((item, i, arr) => {
          const { twoLevel = '' } = item
          let flag = false
          if (twoLevel == twoLevelSelected) {
            flag = true
          }
          return flag
        })
      }
    }
    return importResultListToShow
  }
  renderColumns = (modalData) => {
    const {
      form: { getFieldDecorator }
    } = this.props
    const columns = [
      {
        title: '商品编码',
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode',
        render: (text, record, index) => text
      },
      {
        title: '商品名称',
        width: 100,
        dataIndex: 'productName',
        key: 'productName',
        render: (text, record, index) => text
      },
      {
        title: '规格',
        width: 60,
        dataIndex: 'spec',
        key: 'spec'
      },
      {
        title: '单位',
        width: 60,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '出库地点',
        width: 120,
        dataIndex: 'outLocationCode',
        key: 'outLocationCode',
        render: (text, record) => {
          const { outLocationCode = '', outLocationName = '' } = record
          return `${outLocationCode} ${outLocationName}`
        }
      },
      {
        title: '储位编码',
        width: 100,
        dataIndex: 'storeLocationCode',
        key: 'storeLocationCode'
      },
      {
        title: '可用库存量',
        width: 80,
        dataIndex: 'stock',
        key: 'stock'
      },
      {
        title: '出库数量',
        width: 140,
        dataIndex: 'number',
        key: 'number',
        render: (text, record, index) => {
          const { unit = '' } = record
          const flag = unit && unit.toUpperCase() === 'KG' ? true : false
          return (
            <div className="box_in_table">
              <FormItem hasFeedback>
                {getFieldDecorator(`importResultList[${index}][number]`, {
                  initialValue: text,
                  rules: [
                    {
                      required: true,
                      message: ' ',
                      pattern: flag
                        ? /(^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?)$/
                        : /^[1-9]\d*$/
                    }
                  ]
                })(
                  <Input
                    placeholder={
                      flag ? '请输入正数且不超过两位小数' : '请输入正整数!'
                    }
                    onChange={this.changePlanData.bind(this, index)}
                  />
                )}
              </FormItem>
            </div>
          )
        }
      },
      {
        title: '入库地点',
        width: 120,
        dataIndex: 'enterLocationCode',
        key: 'enterLocationCode',
        render: (text, record) => {
          const { enterLocationCode = '', enterLocationName = '' } = record
          return `${enterLocationCode} ${enterLocationName}`
        }
      },
      {
        title: '备注',
        width: 200,
        dataIndex: 'remark',
        key: 'remark'
      }
    ]
    return columns
  }
  render() {
    const {
      tableLoading,
      selectedRowKeys,
      modalData = [],
      importResultList = []
    } = this.props
    const { twoLevelSelected = 'all' } = this.state
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    const importResultListToShow = this.renderControledTable(
      importResultList,
      twoLevelSelected
    )
    const columns = this.renderColumns(modalData)
    return (
      <div className="table-group table-group-form">
        <Table
          rowKey={(record) => record.productCode}
          loading={tableLoading}
          columns={columns}
          dataSource={importResultListToShow}
          // dataSource={[{}]}
          scroll={{ x: countTableScroll(columns) }}
          onChange={this.onChange}
          pagination={false}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}
export default app



