import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_CREATETRANSFERORDERS_CONFIRM
} from 'api'
//同步
export const update = object => ({
  type: actionType.CREATETRANSFERORDER_UPDATE,
  data: object
})
// 创建/编辑调拨单
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.CREATETRANSFERORDER_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_CREATETRANSFERORDERS_CONFIRM, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.CREATETRANSFERORDER_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      console.log('data is', data)
      callBack && callBack()
    })
    .catch(err => {
      console.log('err is', err)
      dispatch({
        type: actionType.CREATETRANSFERORDER_FAIL
      })
    })
}



