import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Table from './components/./Table'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
import { Form, message, modal } from 'antd'
import style from './components/style/Style.scss'
import { formatterPathAndId } from '../global/components/menuData'
import { getPermissions } from 'util'
const confirm = modal.confirm
@connect(
  (state) => ({
    ...state.creatTransferOrder,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({ importResultList: [] })
  }
  componentWillMount() {
    const { location = {} } = this.props
    const { pathname } = location
    const newKey = pathname.replace(/^\//, '') // 去除开头的斜线('/')
    const b2bUserMenu = JSON.parse(localStorage.getItem('b2bUserMenu') || '[]')
    let id = formatterPathAndId(b2bUserMenu, newKey)
    console.log('id is 111', id, newKey)
    if (id) {
      getPermissions(id).then((res) => {
        this.props.updateUser({ menuPermissionIds: res || '' })
      })
    }
  }
  handleSubmit = (e) => {
    const { form, handleAddOrEdit, importResultList, history } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        confirm({
          title: '温馨提示',
          content: '确定创建调拨单？',
          okText: '确认',
          width: 520,
          cancelText: '取消',
          onOk() {
            handleAddOrEdit(
              {
                data: importResultList
              },
              () => {
                message.success('操作成功')
                history.push('/mms/transferCenter/transferRecord')
              }
            )
          }
        })
      }
    })
  }
  render() {
    const { history, menuPermissionIds } = this.props
    return (
      <section>
        <Bread route={['调拨中心', '创建调拨单']} />
        <Form
          onSubmit={this.handleSubmit}
          autoComplete="off"
          className={style.form}
        >
          <Tool
            history={history}
            form={this.props.form}
            menuPermissionIds={menuPermissionIds}
          />
          <Table
            history={history}
            form={this.props.form}
            menuPermissionIds={menuPermissionIds}
          />
        </Form>
      </section>
    )
  }
}
export default Form.create()(App)



